body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  border: 1px solid #000;
  margin: auto;
  margin-bottom: 25px;
  margin-top: 25px;
  width: 70%;
}

.App header {
  margin: auto;
  text-align: left;
  width: 95%;
}

.App header p {
  font-size: 20px;
}

.pagination {
  margin-bottom: 15px;
  margin-top: 65px;
  text-align: center;
}

.pagination button {
  background-color: #fff;
  color: #338AC6;
  font-size: 14px;
  margin-bottom: 25px;
  padding: 8px;
  text-align: center;
}

.pagination button:hover {
  background-color: #338AC6;
  color: #fff;
}

.pros-filter {
  font-size: 16px;
  margin: auto;
  padding-bottom: 10px;
  padding-top: 10px;
  text-align: left;
  width: 95%;
}

.pros-filter label {
  display: inline-block;
  margin-bottom: 10px;
  margin-right: 25px;
  margin-top: 10px;
}

.pros-filter label select {
  display: block;
  font-size: 16px;
  margin-top: 10px;
}

.pros-filter label input {
  display: block;
  font-size: 16px;
  margin-top: 10px;
}

.pros-filter button {
  font-size: 16px;
  padding-left: 20px;
  padding-right: 20px;
}

.pro-td {
  padding-bottom: 15px;
  padding-left: 5px;
  padding-right: 10px;
  padding-top: 15px;
  overflow: scroll;
}

.pro-tr-grey {
  background-color: #EBEBEB;
}

.black-star {
  font-size: 19px;
}

.star-rating {
  display: inline-block;
}

table {
  border: 1px solid #000;
  margin: auto;
  margin-bottom: 25px;
  margin-top: 25px;
  table-layout: fixed;
  width: 95%;
}

table tbody {
  text-align: left;
}

.table-header {
  background-color: #CBCBCB;
  padding-bottom: 10px;
  padding-left: 5px;
  padding-right: 40px;
  padding-top: 10px;
}

